const { ApiService } = Shopware.Classes;

class ComulatedWeightApiService extends ApiService {
    constructor(httpClient, loginService, apiEndpoint = 'customer-bought') {
        super(httpClient, loginService, apiEndpoint);
        this.name = 'customerBoughtApiService';
    }

    getListOfBoughtProducts(customerId, additionalParams = {}, additionalHeaders = {}){
        let route = `/_action/customer-bought/${customerId}/list`;
        const headers = this.getBasicHeaders(additionalHeaders);

        const params = {};

        return this.httpClient
            .get(route, params, {
                additionalParams,
                headers,
            }).then((response) => {
                return ApiService.handleResponse(response);
            });
    }
}

export default ComulatedWeightApiService;
