import './module/sw-settings';
import './module/sw-settings-cumulated-weight';

import ComulatedWeightApiService from './admin-api/comulated-weight.api.service';

Shopware.Service().register('ComulatedWeightApiService', (container) => {
    const initContainer = Shopware.Application.getContainer('init');
    return new ComulatedWeightApiService(
        initContainer.httpClient,
        container.loginService
    );
});

Shopware.Module.register('sw-settings-index-analytics', {
    routeMiddleware(next, currentRoute) {
        if (currentRoute.name === 'sw.settings.index') {

            currentRoute.children.push({
                name: 'sw.settings.index.analytics',
                path: '/sw/settings/index/analytics',
                component: 'sw-settings-index',
                meta: {
                    parentPath: "sw.settings.index"
                }
            });

        }
        next(currentRoute);
    },
});
